    <div class="container">
      <div class="row">
        <div class="col-md-4">
          <input type="text" v-model="username" class="form-control" placeholder="Username" required>
        </div>
        <div class="col-md-4">
          <input type="text" v-model="tag" class="form-control" placeholder="#Tag" required>
        </div>
        <div class="col-md-4">
          <div class="btn-group" role="group" aria-label="Basic mixed styles example">
            <button @click="proyecto()" class="btn btn-primary"><span class="mdi mdi-magnify"></span> Buscar</button>
            <button @click="exportTableToCSV()" class="btn btn-success"><span class="mdi mdi-table-arrow-down"></span> Descargar CSV</button>
            <a class="btn btn-warning" href="https://valorantstatsamv.shinyapps.io/valorantstats/" target="_blank" role="button"><span class="mdi mdi-poll"></span> Analizar</a>
          </div>
        </div>
      </div>
      <div class="row my-4">
        <div class="col-md-12">
          <ul class="list-group">
            <li class="list-group-item active">Instrucciones</li>
            <li class="list-group-item text-start">Ingresar nombre de usuario y #tag de la cuenta de Riot. Ejemplo: LexDeir#1212.</li>
