              </tr>
            </thead>
            <tbody>
              <tr v-for="match in matches" :key="match">
                <td>{{ match.segments[0].metadata.result }}</td>
                <td>{{ match.segments[0].metadata.agentName }}</td>
                <td>{{ match.metadata.mapName }}</td>
                <td>{{ match.segments[0].stats.roundsPlayed.value }}</td>
