    <!--Se modifica solo data.matches[n] {{ matches[0].data.matches[0].segments[0].stats.deaths.value }}-->
    <!--{{ matches[0].data.matches[0].metadata.result }}-->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'HomeView',
  data(){
    return{
      matches: [],
      num: 0,
      username: '',
      tag: '',
      flag: false,
      config: {
        headers: {
          "Content-Type": "application/json"
        }
      },
    }
  },
  methods:{
    getStats(url){
      this.matches = []
      axios.get(url, this.config)
        .then(response => {
          let data = response.data.data.matches
          if(data.length > 0){
            this.matches = this.matches.concat(data)
            this.flag = true
          }
        })
    },
    proyecto(){
      let next = 0
      //this.getStats("https://api.tracker.gg/api/v2/valorant/standard/matches/riot/"+username+"%23"+tag+"?type=competitive&next="+next)
      
      while(next < 50){
        this.getStats("https://api.tracker.gg/api/v2/valorant/standard/matches/riot/"+this.username+"%23"+this.tag+"?type=competitive&next="+next)
        next++
      }
    },
    downloadCSV(csv, filename) {
      var csvFile;
      var downloadLink;
      csvFile = new Blob([csv], {type: "text/csv"});
      downloadLink = document.createElement("a");
      downloadLink.download = filename;
      downloadLink.href = window.URL.createObjectURL(csvFile);
      downloadLink.style.display = "none";
      document.body.appendChild(downloadLink);
      downloadLink.click();
    },
    exportTableToCSV() {
      var filename = this.username + "#" + this.tag + ".csv"
      var csv = [];
      var rows = document.querySelectorAll("table tr");
      
      for (var i = 0; i < rows.length; i++) {
          var row = [], cols = rows[i].querySelectorAll("td, th");
          
          for (var j = 0; j < cols.length; j++) 
              row.push(cols[j].innerText);
          
          csv.push(row.join(","));        
      }
      this.downloadCSV(csv.join("\n"), filename);
    }
  }
}
</script>
